@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  color: #222;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  font-weight: 300;
  width: 100%;
}

.wrapper {
  width: 90%;
  margin: 50px auto;
}

header {
  position: relative;
}

header .logo {
  font-weight: 700;
  font-size: 30px;
}

header ul.nav {
  float: right;
  list-style: none;
}

header ul.nav li {
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms ease;
}

header ul.nav li:hover {
  opacity: .5;
}

.shop-cart-button {
  float: right;
  cursor: pointer;
  transition: color, transform 500ms ease;
  height: 23px;
}

.shop-cart-button:hover,
.shop-cart-button.active {
  color: #dc3d3d;
  transform: scale(1.3);
}

header .shop-cart {
  position: absolute;
  top: 30px;
  right: 0;
  width: 450px;
  background: #fafafa;
  -webkit-box-shadow: 7px 8px 10px -3px rgba(0,0,0,0.42); 
  box-shadow: 7px 8px 10px -3px rgba(0,0,0,0.42);
  z-index: 1000;
  padding: 20px 20px 0 20px;
  border-radius: 10px;
}

header .shop-cart .item {
  width: 100%;
  float: left;
  margin-bottom: 20px;

}

header .shop-cart .empty h2 {
  font-size: 20px;
  margin-bottom: 20px;
}

header .shop-cart .item h2 {
  font-size: 20px;
  margin-bottom: 10px;

}

header .shop-cart .item p {
  color: #797979;

}


header .shop-cart .item img {
  width: 70px;
  height: 70px;
  float: left;
  margin-right: 20px;
  
}

header .shop-cart .item .delete-icon {
  color: #ca5252;
  float: right;
  position: relative;
  top: -25px;
  cursor: pointer;
  transition: color, transform 500ms ease;
  
}

header .shop-cart .item .delete-icon:hover {
  color: #d83030;
  transform: scale(1.3);
}

header .shop-cart .sum {
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;

}

header .presentation {
  margin: 50px 0;
  background: url('./img/bg.png') no-repeat center center;
  background-size: cover;
  width: 100%;
  height: 500px;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;
}

header .presentation::after {
  content: 'The best products for your home';
  position: absolute;
  top: 100px;
  left: 50px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  color: #fff;
  text-shadow: 1px 1px 4px #bcbcbc;
}

header .presentation::before {
  content: 'at low prices';
  position: absolute;
  top: 250px;
  left: 50px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: #fff;
}

.categories div {
  display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom: 25px;
  margin-right: 15px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
}

.categories div:hover {
  border-color: silver;
  transform: scale(1.1);
}

.full-item {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  overflow: auto;
}

.full-item > div {
  width: 600px;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background: #fff;
  border-radius: 9px;
}

main {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}

main .item,
.full-item .item {
  width: 300px;
  margin-bottom: 50px;
  background: #f5f5f5;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;
}

main .item img
 {
  width: 100%;
  height: 300px;
  transition: transform 500ms ease;
  cursor: pointer;
}

.full-item img {
  width: 80%;
  transition: transform 500ms ease;
  cursor: pointer;
  object-fit: cover;
  max-height: 600px;
}

.full-item .close-btn {
  float: right;
  cursor: pointer;
  font-weight: 600;
  margin: -20px -4px 20px 0;
}

.full-item img:hover,
.full-item .close-btn,
main .item img:hover {
  transform: scale(1.05);
}

main h2,  
main p, 
main b,
.full-item h2,
.full-item p,
.full-item b {
  margin: 10px 20px;
  color: #333;
}

main b,
.full-item b {
  color: #5fa36a;
}

.full-item .add-to-cart,
main .add-to-cart {
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  border-radius: 50px;
  cursor: pointer;
  font-weight: 600;
  transition: transform 500ms ease;
}

main .add-to-cart:hover,
.full-item .add-to-cart:hover {
  transform: scale(1.2) translateY(-2px);
}

footer {
  text-align: center;
  margin-top: 100px;
}